
.Home{
    width:100vw;
}

.Images{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.rect{
    height:12cm;
    width:28cm;
}

.imgDiv{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.arrow{
    height:1cm;
    width:1cm;
    border-radius: 50%;
    background-color: transparent;
}

.arrowButton{
    background-color: transparent;
    border:none;
    border-radius:50%;
}

@media only screen and (min-width:1040px) and (max-width:1200px) {
    .rect{
        width:25cm ;
        height:11cm ;
    }
    
}

@media only screen and (min-width:950px) and (max-width:1040px) {
    .rect{
        width:22cm ;
        height:10cm ;
    }
    
}

@media only screen and (min-width:850px) and (max-width:950px) {
    .rect{
        width:20cm ;
        height:9cm ;
    }
    
}
@media only screen and (min-width:750px) and (max-width:850px) {
    .rect{
        width:17cm ;
        height:9cm ;
    }
    
}

@media only screen and (min-width:630px) and (max-width:750px) {
    .rect{
        width:14cm ;
        height:9cm ;
    }
    
}

@media only screen and (min-width:630px) and (max-width:750px) {
    .rect{
        width:14cm ;
        height:9cm ;
    }
}
